body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.toggle-button {
    display: flex;
    flex-direction: column;
    height: 24px;
    width: 30px;
    background: transparent;
    border: none;
    cursor: pointer;
}

.toggle-button:focus {
    outline: none;
}

.bar1 {
    width: 20px;
    height: 3px;
    background-color:black;
    margin: 2px 0;
    transition: 0.4s;
}

.bar2 {
    width: 20px;
    height: 3px;
    background-color:black;
    margin: 2px 0;
    transition: 0.4s;
}

.bar3 {
    width: 20px;
    height: 3px;
    background-color:black;
    margin: 2px 0;
    transition: 0.4s;
}


#tb{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 2;
    transition: .6s all;
}

#logo{
    transition: .6s all;
}

.toolbar {   
    background: lightblue;
    height: 100px;
}

.toolbar-nav{
    display: flex;
    height: 100%;
    align-items: center;
    padding: 0 1rem;
}

.spacer{
    flex: 1 1;
}

.toolbar-logo a{
    color: white;
    text-decoration: none;
    font-size: 16px;
}

.toolbar-nav-items a{
    color: white;
    text-decoration: none;
    font-size: 16px;
    transition: 0.3s;
}

.toolbar-nav-items a:hover{
    color: blanchedalmond;
    text-decoration: none;
    border-bottom: solid 1px white;
}

.toolbar-nav-items {
    list-style-type: none;
    margin: 0;
    padding: 0;
    display: flex;
}

.toolbar-nav-items li{
    padding: 0 0.8rem;
}

.toggleButton{
    display: none;
}

.smaller{
    height: 60px;
    background: rgb(169, 169, 169, 0.7);
}

.logo-big{
    height: 90px;
}

.logo-small{
    height: 50px;
}

@media screen and (max-width: 992px){
    .toolbar-nav-items{
        display: none
    }

    .toggleButton{
        display: flex;
    }
}
.side-drawer {
    height: 100%;
    background-color: black;
    box-shadow: 1px 0px 7px rgba(0,0,0,0.5);
    position: fixed;
    top: 0;
    right: 0;
    width: 70%;
    max-width: 400px;
    z-index: 200;
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
    transition: 0.4s;
}

.side-drawer.open{
    -webkit-transform: translateX(0);
            transform: translateX(0);
}

.side-drawer ul{
    height: 100%;
    list-style: none;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.side-drawer li{
    margin: 0.5rem 0;
}

.side-drawer a{
    color: grey;
    text-decoration: none;
    font-size: 2rem;
}

.side-drawer a:hover,
.side-drawer a:active{
    color: lightgrey;
}

.tgb{
    right: 0;
    top: 0;
}
.backdrop{
    position: fixed;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.3);
    z-index: 100;
}
.full-screen{
    height: 100%;
    min-width: 100%;
    overflow: hidden;
}

.carousel{
    z-index: 1;
}

.intro{
    background-color: grey;
    height: 260px;
    width: 100%
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

